#roundButton {
    background-color: rgb(29, 29, 29);
    margin-top: 10px;
    margin-bottom: 10px;
    margin-right: 15px;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    border: 5px solid rgb(73, 73, 73);
    outline: none;
    transition: all .2s cubic-bezier(0, 1.26, .8, 1.28);
  }
   
  #roundButton:hover {
    background-color: rgb(29, 29, 29);
    cursor: pointer;
    border-color: rgb(73, 73, 73);
    transform: scale(1.2, 1.2);
  }
   
  #roundButton:active {
    border-color: rgb(73, 73, 73);
    background-color: #FFF;
  }
#flyoutMenu {
    width: 100vw;
    height: 100vh;
    background-color: #ECECEC ;
    position: fixed;
    top: 0;
    left: 0;
    transition: transform .3s
                cubic-bezier(0, .52, 0, 1);
    overflow: scroll;
    z-index: 1000;
    opacity: 80%;
  }
   
  #flyoutMenu.hide {
    transform: translate3d(-100vw, 0, 0);
  }
   
  #flyoutMenu.show {
    transform: translate3d(0vw, 0, 0);
    overflow: hidden;
  }
   
  #flyoutMenu h2 a {
    color: #333;
    margin-left: 15px;
    text-decoration: none;
  }
   
  #flyoutMenu h2 a:hover {
    text-decoration: underline;
  }

  #menuText {
    margin-top: 80px;
    margin-left: 5px;
  }
